import React from 'react';

export interface TabPanelProps extends React.HTMLAttributes<HTMLDivElement> {
  readonly selectedIndex?: number;
  readonly index: number;
}

export const TabPanel: React.FC<TabPanelProps> = ({ selectedIndex, index, children, ...props }) =>
  selectedIndex === index && (
    <div role="tabpanel" hidden={selectedIndex !== index} {...props}>
      {children}
    </div>
  );
